.NavigationItem {
	margin: 10px 0px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: block;
	width: 100%;
}
.NavigationItem a {
	color: #d37f02;
	text-decoration: none;
	width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: block;
}
.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
	color: #40a4c8;
}
@media (min-width: 500px) {
	.NavigationItem {
		margin: 0;
		display: -ms-flexbox;
		display: flex;
		height: 100%;
		width: auto;
		-ms-flex-align: center;
		    align-items: center;
	}
	.NavigationItem a {
		color: #fff;
		height: 100%;
		padding: 16px 10px;
		border-bottom: 4px solid transparent;
	}
	.NavigationItem a:hover,
	.NavigationItem a:active,
	.NavigationItem a.active {
		background-color: #d37f02;
		border-bottom: 4px solid #40a4c8;
		color: #fff;
	}
}



